@charset "utf-8";

.container {
  width: min(100% - 30px, 1080px);
  margin-inline: auto;
}

.flex {
  display: flex;
}

.space-between {
  justify-content: space-between;
}

section {
  padding: 80px 0 65px;
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Header                                               */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
header {
  height: 60px;
  background-color: rgb(102, 27, 155);
}

h1, .header-list a:hover {
  color: #fff;
}

header h1 {
  font-size: 2.2rem;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 0.2px;
}

.header-list li {
  cursor: pointer;
  margin-right: 25px;
  line-height: 60px;
  font-size: 18px;
  font-weight: 500;
}

a {
  text-decoration: none;
}

.header-list a {
  color: rgb(241 227 250);
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Banner                                               */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
.banner {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100svh;
  background: #f8f9fe url('https://images.unsplash.com/photo-1576514129883-2f1d47a65da6?q=80&w=1000&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8M3x8c3RhZ2V8ZW58MHx8MHx8fDA%3D') center 50% / cover no-repeat;
}

main .banner::before {
  position: absolute;
  top: 60px;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgb(0 0 0 / 0.55);
  content: '';
 }

.banner .container {
  margin-bottom: 50px;
  position: relative;
  z-index: 99999;
  text-align: center;
}

.banner h1 {
  font-size: 4.4rem;
  font-weight: 600;
}

.banner-text {
  width: 75%;
  margin: 0 auto;
}

.banner p {
  padding: 20px 0 50px;
  font-size: 1.8rem;
  text-align: center;
  color: #fff;
}

.buttons {
  gap: 25px;
  justify-content: center;
}

button {
  display: block;
  padding-bottom: 3px;
  margin-top: 12px;
  height: 48px;
  width: 130px;
  font-size: 1.8rem;
  font-weight: 600;
  border-radius: 50px;
  border: 1px solid #35a75d;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.primary {
  background-color: #35a75d;
  color: #fff;
}

.secondary {
  color: #35a75d;
}

.primary:hover {
  background-color: #2c8f4e;
  border: 1px solid #2c8f4e;
}

.secondary:hover {
  background-color: #35a75d;
  color: #fff;
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Gallery                                              */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
.purple {
  color: rgb(112 33 168);
}

.gallery h2, .gallery p {
  text-align: center;
}

.gallery h2 {
  font-size: 2.8rem;
  font-weight: 600;
  color: rgb(36 32 45);
}

.gallery p {
  font-size: 16px;
  font-weight: 400;
  color: rgb(81 76 91);
}

.movie-list {
  margin: 40px 0 20px;
  gap: 26px;
}

.movie-list img {
  width: auto;
  border-radius: 5px;
  box-shadow: 1px 1px 4px rgb(189 187 193 / 0.45);
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}

.movie-list img:hover {
  transform: translateY(-4px);
}

.movie-list p {
  padding-top: 8px;
  font-size: 18px;
  font-weight: 500;
  color: rgb(36 32 45);
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Footer                                               */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
footer {
  background-color: #0f0417;
  font-size: 1.8rem;
  font-weight: 400;
  color: #fff;
  line-height: 60px;
  text-align: center;
}

footer p {
  padding-bottom: 30px;
}

.footer-row ul {
  padding: 30px 0 0;
  justify-content: center;
  gap: 25px;
}

footer a {
  color: #b873ea;
}

footer a:hover {
  color: rgb(243, 224, 255);
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Media Queries                                        */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
@media (max-width: 904px) {
  .gallery p {
    font-size: 14px;
  }
}

@media (max-width: 810px) {
  .banner h1 {
    font-size: 4rem;
  }

  .banner p {
    font-size: 1.6rem;
  }

  .header-list li {
    margin-right: 18px;
    font-size: 16px;
  }

}

@media (max-width: 722px) {
  .gallery p {
    font-size: 12px;
  }
}

@media (max-width: 698px) {
  .banner h1 {
    font-size: 3.4rem;
  }

  .banner p {
    font-size: 1.4rem;
  }

  .footer-row ul {
    gap: 12px;
    font-size: 1.6rem;
  }

}

@media (max-width: 602px) {
  .header-list li {
    margin-right: 14px;
    font-size: 14px;
  }
}

@media (max-width: 582px) {
  .footer-row ul {
    font-size: 1.4rem;
  }
}

@media (max-width: 515px) {
  .header-list, .footer-row {
    display: none;
  }
  
  footer p {
    font-size: 1.4rem;
    padding-top: 15px;
  }
}

@media (max-width: 380px) {
  .banner h1 {
    font-size: 3rem;
  }

  .buttons {
    display: inline-block;
    align-items: center;
    justify-content: center;
  }

  .movie-list {
    display: inline-block;
  }

  .movie-list img:hover {
    transform: translateY(0px);
  }

  .gallery p {
    font-size: 16px;
    margin-bottom: 25px;
  }

}